















import Swal from 'sweetalert2';
import { Component, Ref, Vue } from 'vue-property-decorator';
import { Action, State } from 'vuex-class';
import * as commonTypes from '@/types/common';
import { IInputInfo, ILoginPayload } from '@/types/interface';
import { getSnapCode } from '@/utility';
import banner from '../../components/Banner.vue';
import inputFormPanel from '../../components/InputFormPanel.vue';

@Component({
  components: {
    banner,
    inputFormPanel,
  },
})

export default class SetMobile extends Vue {
  @Ref('inputForm') readonly inputForm!: inputFormPanel;
  @State('approveKey') stateKey!: string;
  @State('source') stateSource!: string;
  @State('name') stateName!: string;
  @State('email') stateEmail!: string;
  @State('userId') stateUserId!: string;
  @Action('addMember') addMember!: (params: IInputInfo) => Promise<ILoginPayload>;

  inputList = [commonTypes.INPUT_PHONE, commonTypes.INPUT_CAPTCHA];

  associate() {
    const info: IInputInfo | undefined = this.inputForm.getInput();
    if (info) {
      const params = {
        Status: '1',
        UserGuid: this.stateUserId,
        Mobile: info.Mobile,
        SnapCode: info.SnapCode,
        Source: this.stateSource,
        // eslint-disable-next-line
        Approve_Key: this.stateKey,
        Name: this.stateName,
        Email: this.stateEmail,
      };
      this.addMember(params)
        .then((data: ILoginPayload) => {
          if (data.Code === '0000') {
            Swal.fire({
              width: 300,
              showCloseButton: false,
              background: '#FFFFFF',
              title: '驗證成功',
              text: '成功綁定手機',
              customClass: {
                title: 'modal-title',
                content: 'modal-content',
                confirmButton: 'modal-confirm-button',
              },
              confirmButtonText: '下一步',
            }).then(() => {
              this.$router.push('/member/association');
            });
          }
        })
        .catch((err) => console.error(err));
    }
  }

  sendCode(phone: string) {
    getSnapCode({
      Source: 'CF05',
      Mobile: phone,
    });
  }
}
